
div.page {
  padding: 0 20px 0 20px;
  width: 100%;
  text-align: center;
}
div.page .md-card.impact {
  min-height: 350px;
}
.md-card.card-center {
  margin: 0 auto;
}
.md-card-actions {
  padding: 0 6px 6px 6px;
}
.md-title {
  color: whtie;
}
.card-icon {
  color: white;
}
.md-card-content {
  padding: 10px 10px 0 10px !important;
}
.md-card-header {
  padding: 6px 16px 6px 16px;
}
.md-subhead {
  font-size: 11px;
  height: 15px;
}
.md-drawer {
  background-color: white;
  width: 400px;
}
.md-drawer .form {
  padding: 20px;
}
.tokens-notification {
  margin-bottom: 20px;
}
.tokens-notification .md-title {
  font-size: 18px;
  color: white;
  text-align: left;
  padding-bottom: 10px;
}
.funds-button {
  border: 1px solid white !important;
  padding: 7px;
  float: right;
}
.unlock-info {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 14px;
  font-color: grey;
}
.funding .md-title, .investing .md-title, .impact .md-title {
  color: white;
}
.impact .md-card-header-flex {
  background-color: #8A48DB;
}
.funding .md-card-header-flex {
  background-color: #21B7C5;
}
.investing .md-card-header-flex {
  background-color: #01C0EF;
}
.value-big {
  height: 100px;
  font-family: Avenir;
  font-size: 28px;
  text-align: center;
  padding-top: 65px;
  border-bottom: 1px solid lightgray;
}
.value-subtitle {
  text-align: center;
  font-family: Avenir;
  font-size: 14px;
  color: gray;
}
.action-button {
  margin-top: 10px;
  margin-bottom: 20px;
}
.impact .md-button.action-button.md-primary.md-theme-default {
  background-color: #8A48DB;
}
.funding .md-button.action-button.md-primary.md-theme-default {
  background-color: #21B7C5;
}
.investing .md-button.action-button.md-primary.md-theme-default {
  background-color: #01C0EF;
}
.md-app-content .md-card.investing, .md-app-content .md-card.funding, .md-app-content .md-card.impact {
  margin: 20px 10px 0 10px;
  min-height: 408px;
}
.md-tab {
  padding: 0;
}
.form-container {
  padding: 20px 20px 0 20px;
}
.description.md-card .text, .text {
  font-size: 14px;
  height: auto;
  font-style: italic;
  color: gray;
  padding: 0 16px 16px 16px;
}
.md-drawer .text {
  padding-top: 16px;
}
.description.md-card .md-subheader.md-theme-default {
  color: black;
  font-size: 14px;
  font-weight: bolder;
}
span.address-text {
  font-size: 11px;
  font-weight: bolder;
}
.investing .md-subhead, .funding .md-subhead {
  color: white;
  margin-bottom: 5px;
  opacity: 1;
}
.token-button {
  margin-left: -5px !important;
  padding-top: 3px;
}
.token-button .md-icon.md-theme-default {
  color: white !important;
}
.md-subhead .md-button.md-icon-button.md-theme-default {
  font-size: 12px;
  height: 20px;
  margin-left: -16px;
}
.md-subhead .md-button .md-icon {
  color: white !important;
}
div.button-box {
  text-align: center;
}
div.tab-desc {
  text-align: center;
  color: #898989;
  font-style: italic;
}
div.tab-section-title {
  font-weight: bolder;
  font-size: 16px;
}
div.tab-section-desc {
  margin: 20px 0 20px 0;
  color: #898989;
  font-size: 16px;
}
.tab-section {
  margin-top: 20px;
  padding-left: 10% !important;
  padding-right: 10% !important;
  margin-bottom: 20px;
}
.tab-section .action-button {
  margin-bottom: 0px;
}
.question-icon {
  font-size: 12px !important;
  margin-top: -8px;
  margin-left: -8px;
  color: #898989;
  font-weight: normal;
}



